}

.additional_infos {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 10px;

  .info {
    // flex: 1;
    display: flex;
    flex-direction: column;
    margin-bottom: 5px;
    font-size: smaller;
    font-weight: 600;
    gap: 10px;
  }

  .bars {
    justify-content: flex-start;
    flex: 4;
    display: flex;
    flex-direction: column;
    gap: 10px;

    .distance_bar {
      background-color: #7AA4DA;
      height: 25px;
      transition: width 1s ease-in-out;
      text-align: end;
      color: beige;
      padding-right: 10px;
      padding-top: 3px;
      position: relative;
      border-top-right-radius: 15px;
      border-bottom-right-radius: 15px;

      &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        border-top: 14px solid transparent;
        border-bottom: 14px solid transparent;
        border-left: 14px solid rgb(255, 255, 255);
      }
    }

    .landing_distance_bar {
      background-color: #92C176;
      height: 25px;
      padding-top: 3px;
      transition: width 1s ease-in-out;
      text-align: end;
      color: beige;
      padding-right: 10px;
      position: relative;
      border-top-right-radius: 15px;
      border-bottom-right-radius: 15px;

      &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        border-top: 14px solid transparent;
        border-bottom: 14px solid transparent;
        border-left: 14px solid rgb(255, 255, 255);
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .additional_infos {
    .info {
      flex-direction: column;
      align-items: flex-start;
    }

    .bars {
      overflow: auto;

      .distance_bar {
        background-color: transparent;
        color: black;
        max-width: fit-content;

        &::before {
          content: "";
          position: absolute;
          top: 50%;
          left: 0;
          transform: translate(0, -50%);
          border: none;
        }
      }

      .landing_distance_bar {
        background-color: transparent;
        color: black;
        max-width: fit-content;

        &::before {
          content: "";
          position: absolute;
          top: 50%;
          left: 0;
          transform: translate(0, -50%);
          border: none;
        }
      }
    }
  }
}

