.header {
  box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
  margin-bottom: 30px;
  padding-bottom: 20px;
  .top_header {
    display: flex;
    align-items: center;
    padding: 10px;
    .left {
      flex: 1;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-left: 20px;
      .lang {
        color: gray;
        font-size: 1.5rem;
      }
      .logo {
        display: flex;
        justify-content: flex-end;
        img {
          display: block;
          max-width: 15%;
          height: auto;
        }
      }
    }
    .right {
      flex: 1;
      display: flex;
      justify-content: flex-end;
      gap: 20px;
      font-size: 1em;
    }
  }
  .nav {
    max-width: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    ul {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 20px;
      list-style: none;
      li {
        color: #000;
        font-size: 16px;
        font-weight: 600;
        text-decoration: none;
        &:hover {
          color: rgb(154, 144, 144);
        }
      }
    }
  }

  .responsive_menu {
    display: none;
  }
}

@media screen and (max-width: 768px) {
  .header {
    .top_header {
      .left {
        .logo {
          img {
            max-width: 30%;
          }
        }
      }
    }
    .nav {
      display: none;
    }
    .responsive_menu {
      display: block;
      position: relative;
      .menu_icon {
        display: block;
        width: 30px;
        height: 30px;
        position: relative;
        cursor: pointer;
        width: 95%;
        text-align: end;
        font-size: larger;
      }
      .menu {
        position: absolute;
        top: 100%;
        right: 0;
        width: 50%;
        background-color: #fff;
        box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
        padding: 5px 20px;
        display: none;
      }
      .active {
        display: block;
      }
    }
  }
}

@media print{
  .header{
    display: none;
  }
}
